.banner {
    text-align: center;
    padding-top: 45px;

    @include tablet {
        padding-top: 33px;
    }

    @include mobile {
        padding-top: 15px;
    }
}

.top {
    padding-top: 48px;

    @include tablet { 
        padding-top: 0px
    }
}

.top__desc{ 
    padding-bottom: 64px;

    @include mobile {
        padding-bottom: 30px;
    }
}

.top__image {
    border-radius: 12px;
    overflow: hidden;
    margin: 0;
}

.top__desc-pic {
    font-size: var(--text-small);
    line-height: var(--line-height-large);
    color: var(--gray);
    max-width: 301px;
    margin-top: 32px;

    @include tablet { 
        font-size: var(--text-small-tablet);
        max-width: 402px;
    }

    @include mobile { 
        font-size: var(--text-small-mobile);
        max-width: 195px;
        margin-top: 15px;
    }
}

.desc {
    padding-left: var(--left);

    &--indent {
        margin: 120px 0;
    }

    &--indent-bot {
        margin: 35px 0 85px;
    }

    @include laptop {
        padding-left: var(--left-laptop);
    }

    @include tablet {
        padding-left: 0;

        &--indent-bot {
            margin-bottom: 70px;
        }
    }

    @include mobile {
        &--indent {
            margin: 45px 0;
        }

        &--indent-bot {
            margin: 25px 0 40px;
        }
    }

}
.desc__text {
    max-width: 657px;
    margin-left: auto;

    @include tablet {
        max-width: 100%;
        margin-left: 0;
        padding-left: 45px;

        &--mobile {
            padding-left: 0;
        }
    }

    @include mobile {
        padding-left: 15px;

        &--mobile {
            padding-left: 0;
        }
    }
}

.block {
    position: relative; 
    padding: 87px 0 80px;
    color: var(--color-tx);

    &--indent {
        padding-left: var(--left);
        padding-top: 0;
        margin-left: 180px;
    }

    &--indent-bot {
        padding-bottom: 234px;
    }

    &--indent-bot-small {
        padding-bottom: 180px;
    }

    &--cl-dark {
        color: var(--color-dark);
    }

    @include laptopXs {
        &--indent {
            margin-left: 0;
            padding-left: var(--left-laptop);
        }
    }

    @include tablet { 
        padding: 130px 40px 40px 66px;

        &--indent {
            padding-top: 0;
        }
    }

    @include mobile {
        padding: 97px 20px 30px 32px;

        &--indent {
            padding-top: 0;
        }
    }
}

.block__inner {
    position: absolute;
    top: 0; 
    left: 0; 
    height: 100%; 
    width: 1512px;
    box-shadow: var(--shadow); 
    background-repeat: repeat; 
    background-position: top center;
    background-image: url('../images/foil.png');
    background-color: var(--foil);
    border-radius: var(--radius);

    &:before {
        content: '';
        width: 208px;
        height: 208px;
        background-position: top left;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url("../images/descor.png");
        position: absolute;
        top: -5px;
        left: -10px;
    }

    @include laptop {
       &:before {
        width: 106px;
        height: 106px;
       } 
    }

    @include tablet {
        width: 100%;
        box-shadow: var(--shadow-tablet); 
    }

    @include mobile {
        &:before {
         width: 77px;
         height: 77px;
        } 
     }
}

.block__text {
    position: relative;
    z-index: 2;
}

.source {
    color: var(--gray-light);

    @include mobile {
        padding-bottom: 30px;
    }
}

.source__desc {
    max-width: 657px;
    margin-left: auto;
}
.source__title {
    font-size: var(--text-large);
    font-family: var(--heading-font-family);
    margin-bottom: 25px;

    @include mobile {
        margin-bottom: 20px;
    }
}

.source__desc p {
    font-size: var(--text-middle);
    line-height: var(--line-height-small);
    margin-bottom: 12px;

    &:last-child {
        margin-top: 40px;
        max-width: 630px;
    }

    @include tablet {
        font-size: var(--text-middle-tablet); 
    }

    @include mobile {
        font-size: var(--body-font-size-mobile);
        padding-left: 25px; 
    }
}

.source__block {
    @include mobile {
        padding-left: 15px;
        padding-right: 15px;
    }
}

.source__inner {
    width: 1903px;
    left: -85px;

    @include desktop {
        left: 0;
    }


}