.article {
    margin-bottom: 120px;

    &.is-active {

        .article__time {
            position: fixed;
            bottom: auto;
            top: 55px;
            z-index: 50;

            @include tablet {
                position: static;
            }
        }

        @for $i from 1 through 20 {
            &:nth-child(#{$i}) {
                .article__time {
                    top: (#{$i*60px + 20px});
                }
            }
        }
    }

    @include desktop {
        &.is-active {
            .article__time {
                top: 5px;
                left: 0;
            }

            @for $i from 1 through 20 {
                &:nth-child(#{$i}) {
                    .article__time {
                        top: (#{$i*60px - 15px});
                    }
                }
            }
        }
    }

    @include tablet {
        margin-bottom: 80px;

        &.is-top {
            .article__time {
                transform: none;
            }
        }
    }

    @include mobile {
        margin-bottom: 50px;
    }
}

.article__top {
    padding-left: var(--left);
    margin-bottom: 120px;
    position: relative;

    @include laptop {
        margin-bottom: 170px
    }

    @include tablet {
        padding-left: 0;
        margin-bottom: 190px;
    }

    @include mobile {
        padding-left: 0;
        margin-bottom: 90px;
    }
}

.article__time {
    position: absolute;
    bottom: -26px;
    z-index: 5;
    transform: translateX(-350px);
    cursor: pointer;
    z-index: 4;

    @include desktop {
        display: block;
        transform: none;
        left: -40px;
        bottom: -8px;
    }

    @include tablet {
        width: 100%;
        position: static;
        transform: none !important;
    }

    @include mobile {
        width: 277px;
    }
}

.article__title {
    position: relative;

    &:before {
        content: 'Â«';
        padding-right: 3px;
    }

    &:after {
        content: 'Â»';
        padding-left: 3px;
    }

    &:before, 
    &:after {
        font-size: inherit;
        font-weight: 350;
        font-family: var(--body-font);
    }

    @include laptop {
        font-size: var(--h2-laptop);
        margin-bottom: 15px;
    }

    @include tablet {
        font-size: var(--h2-tablet);
    }

    @include mobile {
        font-size: var(--h2-mobile);
    }
}

.article__text {
    color: var(--gray-light);
    margin-top: -130px;

    &--top-small {
        margin-top: -70px;
    }

    &--2 {
        .article__mark-text {
            margin-bottom: 43px;

            @include mobile {
                margin-bottom: 20px;
            }
        }
    }

    &--3 {
        padding-top: 20px;
    }

    &--4 {
        .article__mark-text {
            margin-bottom: 42px;
        }

        @include tabletMin {
            .article__mark-text p {
                margin-left: -185px;
            }
        }

        p:nth-child(3) {
            margin-bottom: 22px;
        }
    }

    &--5 {
        p {
            line-height: 127%;
        }
        @include tabletMin {
            .article__mark-text {
                margin-top: 77px;
            }
    
            .article__mark-text p {
                margin-left: -205px;
            }
        }
    }

    &--6 {
        @include tabletMin {
            padding-top: 40px;

            .article__mark-text {
                margin-bottom: 0px;
            }

            p {
                line-height: 130%;
            }
        }
    }

    &--7 {
        @include tabletMin {
            p {
                line-height: 131%;
            }

            .article__mark-text {
                margin-top: 65px;
            }

            .article__mark-text p {
                margin-left: -170px;
            }

        }
    }

    &--8 {
        @include tabletMin {
            p:first-child {
                max-width: 631px;
            }

            .article__mark-text {
                transform: translateY(-15px);
                margin-bottom: 28px;
            }

            .article__mark-text p {
                margin-left: -190px;
            }
            .article__mark-text p:first-child{
                max-width: 120%;
            }

            p:last-child {
                line-height: 130%;
            }
        }
    }

    &--9 {
        @include tabletMin {

            .article__mark-text {
                margin-bottom: 35px;
            }

            p {
                line-height: 130%;
            }
        }
    }

    &--10 {
        @include tabletMin {
            .article__mark-text p:last-child {
                max-width: 849px;
            }
        }
    }

    &--11 {
        @include tabletMin {

            .article__mark-text {
                margin-bottom: 35px;
                margin-bottom: 10px;
            }

            .article__mark-text p {
                margin-left: -200px;
                &:last-child {
                    margin-top: -22px;
                }
            }

            p {
                line-height: 125%;

                &:last-child {
                    margin-top: 45px;
                }
            }
        }
    }
    &--top {
        margin-top: 0;
        padding-top: 140px;
    }

    @include tablet {
        margin-top: -130px;
    }

    @include mobile {
        margin-top: -80px;

        &--top {
            padding-top: 80px;
        }
    }
}

.article__text p {
    max-width: 671px;

    @include tablet {
        font-size: 4.8vw;
        line-height: 120%;
        margin-bottom: var(--paragraph-margin-tablet);
    }

    @include mobile {
        font-size: 4.9vw;
        margin-bottom: var(--paragraph-margin-mobile);
    }
}

.article__text--2 p:last-child {
    line-height: 129%;
}

.article__text--10 p:last-child {
    max-width: 620px;
}

.article__mark-text {
    margin-bottom: 18px;

}

.article__mark-text p {
    max-width: 873px;
    margin-top: -22px;
    margin-left: -140px;

    @include tablet {
        margin-left: -35px;
        margin-top: 0;
    }

    @include tablet {
        margin-left: -15px;
    }
}

.article__mark-text strong {
    line-height: 120%;
}

.article__image {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    transform: translateY(-157px);

    &--indent {
        padding-right: 29%;
    }

    &--indent-small {
        padding-right: 14%;
    }

    &--top-small {
        transform: translateY(-100px);  
    }

    &--3 {
        transform: translateY(-190px);  
    }

    &--default {
        transform: none;
        margin-top: 75px;
    }

    @include tablet {
        transform: translateY(-135px); 
        padding-right: 0;
        margin-bottom: 35px;

        &--default {
            transform: none;
        }
    }

    @include mobile {
        max-width: 84%;
        margin-left: auto;
        transform: translateY(-66px); 

        &--default {
            transform: none;
            margin-top: 30px;
        }

        &--top-small {
            max-width: 77%;
        }
    }
}

.article__image picture {
    display: inline-block;
    box-shadow: var(--shadow);
    border-radius: var(--radius-small);

    @include tablet {
        box-shadow: var(--shadow-tablet);
    }
}

.article__image-desc {
    font-size: 12px;
    color: var(--gray);
    margin-top: 25px;
    
    span {
        display: block;
    }

    @include tablet {
        font-size: var(--text-small-tablet);
        max-width: 368px;
    }

    @include tablet {
        font-size: var(--text-small-mobile);
        margin-top: 15px;
    }
}

.article__mask {
    width: 1557px;
    position: absolute;
    top: 97px;
    left: -29px;

    &--2 {
        width: 1670px;
        top: 87px;
        left: -38px;
    }

    &--3 {
        width: 1538px;
    }

    &--4 {
        width: 1589px;
        left: -45px;

        @include tabletMin { 
            top: 184px;
        }
    }

    &--5 {
        width: 1491px;

        @include tabletMin { 
            top: 158px;
            left: -45px;
        }
    }

    &--6 {

        @include tabletMin { 
            top: 75px;
            left: -35px;
        }
    }

    &--7 {
        width: 1506px;
        @include tabletMin { 
            top: 103px;
            left: -8px;
        }
    }

    &--8 {
        @include tabletMin {
            width: 1537px;
            top: 84px;
            left: -45px;
        }
    }

    &--9 {
        @include tabletMin {
            width: 1537px;
            top: 119px;
            left: -35px;
        }
    }

    &--10 {
        width: 1590px;
        top: 40px;
        left: -48px;
    }

    &--11 {
        @include tabletMin {
            width: 1551px;
            top: 55px;
            left: -26px;
        }
    }

    @include tablet {
        width: calc(100% + 40px);
        height: calc(100% + 30px);
    }

    @include mobile {
        top: 50px;
        left: -30px;
        width: calc(100% + 34px);
    }
}
.article__mask-inner {
    transition: var(--transition);
    transform-origin: 0 0;

    @include tablet {
        border-radius: 12px;
        box-shadow: var(--shadow-mask);
        height: 100%;
        overflow: hidden;

        picture,
        img {
            height: 100%;
            object-fit: cover;
            object-position: top left;
        }
    }
}

.article__mask.is-active .article__mask-inner {
    transform: translateX(43%) translateY(60px) rotate(-7deg);

    @include tablet {
        transform: translateX(90%) translateY(40px) rotate(-3deg);
    }
}

.article__mask picture,
.article__mask img {
    width: 100%;
}
.article__about {
    margin-top: 160px;

    &--small {
        margin-top: 70px;
    }

    @include tablet { 
        margin-top: 100px;
    }

    @include mobile { 
        margin-top: 80px;
    }
}

.article__about strong {
    margin-right: 5px;
}

.article__author {
    @include mobile {
        margin-bottom: 15px;
    }
}
