[data-animation],
[data-animation-start] {
  opacity: 0;
  transition: 3.5s var(--anim);
}

[data-animation=right] {
  transform: translate3d(180px, 0, 0);
}

[data-animation=right-slow] {
  transform: translate3d(180px, 0, 0);
  transition-delay: 0.4s !important;
}

[data-animation=right-slow-more] {
  transform: translate3d(180px, 0, 0);
  transition-delay: 0.75s !important;
}

[data-animation-start=right-large] {
  transform: translate3d(380px, 0, 0);
}

[data-animation=fade] {
  opacity: 0;
}

[data-animation].visible {
  opacity: 1;
  transform: translate3d(0, 0, 0) !important;
  transition: 2s var(--anim);
}

[data-animation-start].visible {
  opacity: 1;
  transform: translate3d(0, 0, 0) !important;
}

