// Variables
// ---------
:root {
	--body-font-size                            : 24px;
	--body-font                                 : "ABCFavoritPro-Book", system-ui, -apple-system, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
	--body-font-alt                             : Georgia, Times, Times New Roman, serif;
	--body-line-height                          : 134%;
	--body-color                                : #000000;
	--body-background                           : #FFF;

	// Text
	--heading-font-weight                       : 700;
	--heading-line-height                       : 1.24;
	--heading-margin                            : 0 0 38px;
	--heading-font-family						: "Grato Grotesk";
	--paragraph-margin                          : 34px;
	--list-margin                               : 1em 0;
	--h1                                        : 28px;
	--h2                                        : 70px;
	--h3                                        : 24px;
	--h4                                        : 18px;
	--h5                                        : 16px;
	--bold-font-weight                          : 700;
	--text-large                                : 22px;
	--text-middle                               : 18px;
	--text-small                                : 16px;
	--line-height-large							: 137%;
	--line-height-small							: 125%;

	// Color
	--main                                      : var(--body-color);
	--gray 										: rgba(16, 20, 61, 0.50);
	--gray-light								: rgba(16, 20, 61, 0.80);
	--color-tx									: #3D4062;
	--color-dark							    : #10143D;

	//Foil 
	--foil										: rgb(238, 241, 243);

	// Shadow
	--shadow									: rgb(56, 54, 158) 5px 5px 0px 0px;

	// Radius
	--radius									: 10px;
	--radius-small								: 8px;

	// Border 
	--borColor 									: rgba(146, 147, 199, 0.8);

	//Indent 
	--left 										: 254px;

	// Transition
	--transition 								: transform 1s ease, opacity 1s ease;

	// Swiper
	--swiper-theme-color                        : var(--main);
	--swiper-preloader-color                    : var(--swiper-theme-color);
	--swiper-wrapper-transition-timing-function : initial;

	// Laptop breakpoint 
	--h2-laptop									: 54px;
	--left-laptop 								: 170px;

	// Animation								
	--anim 										: cubic-bezier(.19,1,.22,1);

	// tablet breakpoint 
	--body-font-size-tablet                     : 36px;
	--body-line-height-tablet                   : 122%;
	--h2-tablet									: 64px;
	--h3-tablet									: 36px;
	--text-middle-tablet                        : 32px;
	--paragraph-margin-tablet                   : 46px;
	--text-small-tablet                         : 22px;
	--shadow-tablet 							: rgb(56, 54, 158) 2px 3px 0px 0px;
	--shadow-mask								: rgba(150, 151, 201) 3px 3px 2px 0px;

	// mobile breakpoints
	--body-font-size-mobile                     : 18px;
	--body-line-height-mobile                   : 111%;
	--h2-mobile									: 30px;
	--h3-mobile									: 18px;
	--text-small-mobile                         : 10px;
	--text-middle-mobile                        : 22px;
	--paragraph-margin-mobile                   : 15px;
}

// Breakpoints
$desktop: (1400 - 0.02) * 1px;
$laptop: (1250 - 0.02) * 1px;
$laptopXs: (1100 - 0.02) * 1px;
$tabletMin: (992 + 0.02) * 1px;
$tablet: 992px;
$tablet2: 768px;
$mobile: 576px;